.layout {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  width: 100%;
}

.layout__side {
  overflow: auto;
}

.layout__main {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

textarea {
  resize: none;
}

.map {
  height: 100%;
  width: 100%;
}

.geo-json {
  display: block;
  padding: 6px;
  white-space: nowrap;
}

.tile-radio {
  display: block;
  padding: 6px;
  white-space: nowrap;
}

.marker {
  transition: -webkit-transform 500ms ease-in-out;
  transition: transform 500ms ease-in-out;
  transition: transform 500ms ease-in-out, -webkit-transform 500ms ease-in-out;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.app__nav {

}

.app__nav-link {
  display: inline-block;
  margin: 5px;
}

.app__content {
  display: -webkit-flex;
  display: flex;
  height: 100%;
}

fieldset {
  border: 1px solid lightgray;
}

html, body, #root {
  height: 100%;
  width: 100%;
}

body {
  background-color: #2b2b2b;
  color: #a9b7c6;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

a {
  color: #589df6;
}

code {
  background-color: #3a3a3a;
  border: 1px solid #4d4d4d;
  color: #c3c3c3;
  display: block;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  padding: 0.5em;
  white-space: pre;
}

fieldset {
  border-color: #505050;
}

input, textarea, select, button {
  background-color: #3a3a3a;
  border-color: #3c3c3c;
  color: #8c8c8c;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: 18px;
}

button {
  background-color: #4c5052;
  border: 1px solid #5e6060;
  border-radius: 2px;
  color: #a8baba;
}

